

const LOCATION_DATA = {
    1: 'H Ch Minh',
    2: 'Long An',
    3: 'ng Nai',
    4: 'Vng Tu',
    5: 'Bnh Thun',
    6: 'Lm ng',
    7: 'Bnh Dng',
    8: 'Ty Ninh',
    9: 'Dak Nong',
    10: 'Dak Lak',
    11: 'Bnh Phc',
    12: 'Tr Vinh',
    13: 'An Giang',
    14: 'Cn Th',
    15: 'Hu Giang',
    16: 'Kin Giang',
    17: 'C Mau',
    18: 'Sc Trng',
    19: 'Bc Liu',
    20: 'ng Thp',
    21: 'Bn Tre',
}

export default class Location {

    static getName(id) {
        for (const key in LOCATION_DATA) {
            if (id == key) {
                return LOCATION_DATA[key];
            }
        }
        return null;
    }

    static getList() {
        let rs = []
        for (const key in LOCATION_DATA) {
            let r = {key: key, value: LOCATION_DATA[key]}
            rs.push(r);
        }
        return rs;
    }
}