export class UserRole{

    constructor(roleId) {
        this.roleId = roleId;
    }

    static findByRoleId(roleId) {
        switch(roleId) {
            case 1: return ADMIN;
            case 2: return CUSTOMER;
            case 3: return EMPLOYEE;
            case 4: return ANONYMOUS;
            default: return null;
        }
    }

    getRoleId() {
        return this.roleId;
    }

    getName() {
        switch(this.roleId) {
            case 1: return "Admin";
            case 2: return "Nh thu";
            case 3: return "Nhn vin";
            case 4: return "Ngi quan tm";
            default: return null;
        }
    }
}

export const ADMIN = new UserRole(1);
export const CUSTOMER = new UserRole(2);
export const EMPLOYEE = new UserRole(3);
export const ANONYMOUS = new UserRole(0);